                                span Address
                                p 188 name st, el mohandseen, Egypt
                            div(class='info-box')
                                i(class='fas fa-envelope')
                                span Email
                                p
                                    a(href='mailto:mail@mai.com') mail@mai.com -
                                    a(href='mailto:mail@yahoo.com') mail@yahoo.com
                            div(class='info-box')
                                i(class='fas fa-phone-alt')
                                span phone
                                p
                                    a(href='tel:201988209222') 201988209222
                            div(class='info-box')
                                i(class='fab fa-whatsapp')
                                span WhatsApp
                                p 
                                    a(href='') 201988209222
                    div(class="col-md-6 col-lg-6")
                        .branchs 
                            .branch
                                .icon 
                                    img(src="@/assets/images/icons/01.png", alt="")
                                .text 
                                    h3 Iraq branch
                                    p
                                        a(href="tel:+51 0292811100") +51 0292811100 
                            .branch
                                .icon 
                                    img(src="@/assets/images/icons/02.png", alt="")
                                .text 
                                    h3 Kuwait branch
                                    p
                                        a(href="tel:+51 0292811100") +51 0292811100 
        div(class="contact-form")
